import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { EnumConfirmation } from 'src/models/Enum/EnumAuthentication.enum';
import { DefaultModel } from 'src/models/PaymentMethods/default.model';
import { DeleteModel } from 'src/models/PaymentMethods/delete.model';
import { PaymentService } from 'src/services/payments/payment.service';
import {confirm} from 'src/models/Confirm/confirm.model';
import { TranslateService } from '@ngx-translate/core';
import { FundsOptionsComponent } from 'src/app/PopUp/funds-options/funds-options.component';
import { ManageDataService } from 'src/services/manage-data.service';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { PaymentMethodsComponent } from '../payment-methods/payment-methods.component';
import { ActionPaymentMethod } from 'src/models/PaymentsMethodsPopUp/paymentMethodPopUp.model';

@Component({
  selector: 'app-funds',
  templateUrl: './funds.component.html',
  styleUrls: ['./funds.component.scss']
})
export class FundsComponent implements OnInit, OnDestroy {

  fundsIn = [];
  fundsOut = [];

  found_data:any;
  direction:any;

  menu:any = false;
  main:any = true;

  public subscription: Subscription = new Subscription();

  constructor( private translate:TranslateService ,private dialog: MatDialog, private payments_service:PaymentService, private router: Router,private manage_data: ManageDataService<any>) { }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit(): void {
    this.FoundsMethods();
  }

  async FoundsMethods(){
    this.fundsIn = await this.GetFoundsInMethods().then(data=>data);
    this.fundsOut = await this.GetFoundsOutMethods().then(data=>data);

    console.log("Metodos de retiro", this.fundsOut);
    console.log("Metodos de deposito", this.fundsIn);


  }

  FundsMenu(data:any, direction:string){

    this.found_data = data;
    this.direction = direction;

    if(this.direction == 'in'){
      this.menu = true;
      this.main = false;

    }else if(this.direction == 'out'){
      this.menu = true;
      this.main = false;

    }

  }

  async Details(data:any, direction:string):Promise<void>{
    data.direction = direction;
    data.action = 'Add';
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";
    
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.FundsOption.toString(),data);
    let dialogRef = this.dialog.open(FundsOptionsComponent,dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if(result == EnumConfirmation.delete.toString()){
        this.Delete(data);
      }else if(result == EnumConfirmation.default.toString()){
        this.Default(data, direction);
      }
  
    }));
  }

  async Delete(data:any){
    const pay_method:DeleteModel = {
      client_payment_method_id: data.client_payment_method_id
    }
    var response = await this.DeleteClientPaymentMethod(pay_method).then(data=>data);
    
    console.log("Response",response);
    this.FoundsMethods();
    this.Back();

  }

  async Default(data:any, direction:any){
    const pay_method:DefaultModel = {
      client_payment_method_id: data.client_payment_method_id,
      direction: direction
    }
    var response = await this.SetDefaultPaymentMethod(pay_method).then(data=>data);
    this.FoundsMethods();
    this.Back();
    console.log("Response",response);

  }

  Back(){
    this.menu = false;
    this.main = true;
  }

  AddPaymentMethods(){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "95vw";
     dialogConfig.maxWidth= '100vw';
    const action:ActionPaymentMethod = {
      action: 'Add'
    }
    this.manage_data.emitEvent(EnumEventsPopUp.ActionPaymentMethods.toString(),action);
    let dialog = this.dialog.open(PaymentMethodsComponent,dialogConfig);
    this.subscription.add(dialog.afterClosed().subscribe(()=>{
      this.FoundsMethods();
    }))
    // this.router.navigate(['/home/payment_methods'])
  }

  GetFoundsInMethods():Promise<any>{
    const data:any ={
      direction: 'in'
    }
    return new Promise<any>((resolve, reject) => {
      this.subscription.add(this.payments_service.GetClientPaymentMethods(data).subscribe(data=>resolve(data),err=>{
        console.warn("Error",err);
        resolve(null);
      }))
    })
  }

  GetFoundsOutMethods():Promise<any>{
    const data:any ={
      direction: 'out'
    }
    return new Promise<any>((resolve, reject) => {
      this.subscription.add(this.payments_service.GetClientPaymentMethods(data).subscribe(data=>resolve(data),err=>{
        console.warn("Error",err);
        resolve(null);
      }))
    })
  }

  DeleteClientPaymentMethod(data:DeleteModel):Promise<any>{
    return new Promise<any>((resolve, reject) => {
      this.subscription.add(this.payments_service.DeleteClientPaymentMethod(data).subscribe(data=>resolve(data),err=>{
        console.warn("Error",err);
        resolve(null);
      }))
    })
  }

  SetDefaultPaymentMethod(data:DefaultModel):Promise<any>{
    return new Promise<any>((resolve, reject) => {
      this.subscription.add(this.payments_service.SetDefaultPaymentMethod(data).subscribe(data=>resolve(data),err=>{
        console.warn("Error",err);
        resolve(null);
      }))
    })
  }

}
